import React, { Component, useEffect, useState } from 'react';
import AMapLoader from '@amap/amap-jsapi-loader';
import '../styles/MapContainer.css';

function MapComponent(locations) {
  // constructor() {
  //   super();
  //   this.map = {};
  // }
  const [map, setMap] = useState(null);
  useEffect(() => {
    AMapLoader.load({
      key: 'baf80b64f3b29ca5655dc11991bf2ca8', // WebKey load 
      version: '2.0', //  JSAPI  1.4.15
      plugins: ['AMap.ToolBar', 'AMap.Driving', 'AMap.AutoComplete'],
      AMapUI: {
        version: "1.1",
        plugins: [],

      },
      Loca: {
        version: "2.0.0"
      }
    })
      .then(AMap => {
        var newMap = new AMap.Map('container', {
          // id
          viewMode: '3D', // 3D
          zoom: 5, // 
          center: [105.602725, 37.076636], // 
        });

        // let positionArr = [
        //   [113.357224, 34.977186],
        //   [114.555528, 37.727903],
        //   [112.106257, 36.962733],
        //   [109.830097, 31.859027],
        //   [116.449181, 39.98614],
        // ];
        let positionArr = locations;
        console.log("this.props.locations");
        console.log(positionArr);
        for (let item of positionArr.locations) {
          let marker = new AMap.Marker({
            position: [item[0], item[1]],
          });
          newMap.add(marker);
        }
        setMap(newMap);

        // var autoOptions = {
        //   //city 
        //   city: ''
        // };
        // // AutoComplete
        // var autoComplete = new AMap.AutoComplete(autoOptions);
        // // 
        // autoComplete.search("", function (status, result) {
        //   // result
        //   console.log("result:")
        //   console.log(result);
        // })

      })
      .catch(e => {
        console.log(e);
      });
  }, [])


  return (
    <div id="container" className="map" style={{ height: '800px' }}></div>
  );
}
// 
export default MapComponent;

